import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType, onlyNumber } from 'summer';
import { CatRegistrada } from '../../../../models/dtos/mfp/catRegistrada';

const CatRegistradaEditView: FC<DetailEditViewFormProps<CatRegistrada>> = props => {
  const formProps = useForm<CatRegistrada>({
    defaultValues: { ...props.selectedItem },
    mode: 'onBlur',
  });

  const maxWidthLabel = 150;

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps}>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true } }}
            id="dataDoRegistro"
            label="Data do Registor"
            labelWidth={maxWidthLabel}
            placeholder="Informe a Data do Registro"
            isPrependLabel={true}
            type={InputType.DATE}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { ...onlyNumber(0, 13), required: true } }}
            id="numeroCat"
            labelWidth={maxWidthLabel}
            isPrependLabel={true}
            label="Número Cat"
            placeholder="Informe O número CAT"
          />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default CatRegistradaEditView;
