import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType } from 'summer';
import { Profissiografia } from '../../../../models/dtos/mfp/profissiografia';

const ProfissiografiaEditView: FC<DetailEditViewFormProps<Profissiografia>> = props => {
  const formProps = useForm<Profissiografia>({
    defaultValues: { ...props.selectedItem },
    mode: 'onBlur',
  });

  const validateExtras = () => {
    const erros = [];

    if (formProps.getValues().dataInicio > formProps.getValues().dataFim) {
      erros.push('A data de inicio não pode ser maior que a data do fim');
    }

    return erros;
  };

  const maxWidthLabel = 150;

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps} validateExtras={validateExtras}>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true } }}
            id="dataInicio"
            label="Data de Início"
            placeholder="Informe a Data de Início"
            labelWidth={maxWidthLabel}
            isPrependLabel={true}
            type={InputType.DATE}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true } }}
            id="dataFim"
            label="Data do Fim"
            labelWidth={maxWidthLabel}
            placeholder="Informe a Data do Fim"
            isPrependLabel={true}
            type={InputType.DATE}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true, maxLength: 400 } }}
            id="descricaoAtividade"
            isPrependLabel={true}
            label="Descrição das Atividades"
            placeholder="Informe a Descrição das Atividades"
            type={InputType.TEXT_AREA}
          />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default ProfissiografiaEditView;
