import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType } from 'summer';
import { ExameMedicoClinicoComplementar } from '../../../../models/dtos/mfp/exameMedicoClinicoComplementar';
import { EXAME_REFERENCIAL_SEQUENCIAL_ENUM_VALUES } from '../../../../models/enumerated/ger/exameReferencialSequencialEnum';
import { INDICACAO_RESULTADO_ENUM_VALUES } from '../../../../models/enumerated/ger/IndicacaoResultadoEnum';
import { TIPO_EXAME_MEDICO_ENUM_VALUES } from '../../../../models/enumerated/mfp/tipoExameMedicoEnum';

const ExameMedicoClinicoComplementarEditView: FC<DetailEditViewFormProps<ExameMedicoClinicoComplementar>> = props => {
  const formProps = useForm<ExameMedicoClinicoComplementar>({
    defaultValues: { ...props.selectedItem },
    mode: 'onBlur',
  });

  const maxWidthLabel = 250;

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps}>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true } }}
            id="dataDoRegistro"
            label="Data de Início"
            labelWidth={maxWidthLabel}
            placeholder="Informe a Data de Início"
            isPrependLabel={true}
            type={InputType.DATE}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="tipoExame"
            label="Fator de Risco"
            isPrependLabel={true}
            labelWidth={maxWidthLabel}
            placeholder="informe o Fator de Risco"
            formProps={formProps}
            inputDropDownProps={{
              optionsProvider: TIPO_EXAME_MEDICO_ENUM_VALUES,
              selectProps: { isClearable: false },
            }}
            type={InputType.DROP_DOWN}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="exameReferencialSequencial"
            label="Exame (Referencial/Sequencial)"
            isPrependLabel={true}
            labelWidth={maxWidthLabel}
            placeholder="Informe o Exame (Referencial/Sequencial)"
            formProps={formProps}
            inputDropDownProps={{
              optionsProvider: EXAME_REFERENCIAL_SEQUENCIAL_ENUM_VALUES,
              selectProps: { isClearable: false },
            }}
            type={InputType.DROP_DOWN}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="indicacaoResultado"
            label="Indicação de Resultado"
            isPrependLabel={true}
            labelWidth={maxWidthLabel}
            placeholder="Informe a Indicação de Resultado"
            formProps={formProps}
            inputDropDownProps={{
              optionsProvider: INDICACAO_RESULTADO_ENUM_VALUES,
              selectProps: { isClearable: false },
            }}
            type={InputType.DROP_DOWN}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 50 } }}
            id="natureza"
            isPrependLabel={true}
            labelWidth={maxWidthLabel}
            label="Descrição Fator de Risco"
            placeholder="Informe a descrição do fator de risco"
          />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default ExameMedicoClinicoComplementarEditView;
