import cx from 'classnames';
import React, { FC, MouseEvent, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Nav, NavItem, NavLink, Row, TabContent, TabPane } from 'reactstrap';
import {
  Accordion,
  AccordionItem,
  ColumnsProps,
  DetailManagerForm,
  DetailManagerFormProps,
  EditForm,
  EditViewProps,
  enumColumn,
  getSelectedItem,
  InputGroupInline,
  OrderType,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, Ppp } from '../../../../models/dtos/mfp/ppp';
import { EXAME_REFERENCIAL_SEQUENCIAL_ENUM_VALUES } from '../../../../models/enumerated/ger/exameReferencialSequencialEnum';
import { INDICACAO_RESULTADO_ENUM_VALUES } from '../../../../models/enumerated/ger/IndicacaoResultadoEnum';
import { TIPO_FATOR_RISCO_ENUM_VALUES } from '../../../../models/enumerated/ger/tipoFatorRiscoEnum';
import { TIPO_EXAME_MEDICO_ENUM_VALUES } from '../../../../models/enumerated/mfp/tipoExameMedicoEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import VinculoIS from '../../../components/mfp/vinculoIS/VinculoIS';
import CatRegistradaEditView from './CatRegistradaEditView';
import ExameMedicoClinicoComplementarEditView from './ExameMedicoClinicoComplementarEditView';
import FatorRiscoEditView from './FatorRiscoEditView';
import LotacaoAtribuicaoEditView from './LotacaoAtribuicaoEditView';
import MonitoracaoBiologicaEditView from './MonitoracaoBiologicaEditView';
import ProfissiografiaEditView from './ProfissiografiaEditView';
import RegistroAmbienteEditView from './RegistroAmbientalEditView';

interface PppEditViewProps extends EditViewProps<Ppp> {}

const defaultWidth: number = 100;

const PppEditView: FC<PppEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<Ppp>({ defaultValues: selectedItem, mode: 'onBlur' });
  const [activeTab, setActiveTab] = useState(1);
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const toggleTab = ({ target }: MouseEvent) => {
    const { tabIndex } = target as HTMLElement;
    if (activeTab !== tabIndex) {
      setActiveTab(tabIndex);
    }
  };

  useEffect(
    () => {
      if (globalReducer.globalParameter.empresaMFP !== null && globalReducer.globalParameter.empresa !== null) {
        const {
          empresaMFP: { id },
        } = globalReducer.globalParameter;

        setTimeout(() => {
          // @ts-ignore
          formProps.setValue('empresaMFP.id', id);
        }, 500);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const columnsLotacaoAtribuicao: ColumnsProps[] = [
    {
      dataField: 'dataInicio',
      text: 'Data de Início',
    },
    {
      dataField: 'dataFim',
      text: 'Data do Fim',
    },
    {
      dataField: 'cnpjCei',
      text: 'CNPJ/CEI',
    },
    {
      dataField: 'setor',
      text: 'Setor',
    },
    {
      dataField: 'funcao',
      text: 'Função',
    },
    {
      dataField: 'codigoGfip',
      text: 'GFIP',
    },
  ];

  const columnsCatRegistrada: ColumnsProps[] = [
    {
      dataField: 'dataDoRegistro',
      text: 'Data do Registro',
    },
    {
      dataField: 'numeroCat',
      text: 'Número CAT',
    },
  ];

  const columnsProfissiografia: ColumnsProps[] = [
    {
      dataField: 'dataInicio',
      text: 'Data de Início',
    },
    {
      dataField: 'dataFim',
      text: 'Data do Fim',
    },
    {
      dataField: 'descricaoAtividade',
      text: 'Descrição das Atividades',
    },
  ];

  const columnsFatorRisco: ColumnsProps[] = [
    enumColumn(
      {
        dataField: 'tipoFatorRisco',
        text: 'Tipo do Fator de Risco',
      },
      TIPO_FATOR_RISCO_ENUM_VALUES
    ),
    {
      dataField: 'fatorRisco',
      text: 'Fator de Risco',
    },
    {
      dataField: 'intensidadeConcentracao',
      text: 'Intensidade Concentração',
    },
    {
      dataField: 'tecnicaUtilizada',
      text: 'Técnica Utilizada',
    },
    {
      dataField: 'dataInicio',
      text: 'Data de Início',
    },
    {
      dataField: 'dataFim',
      text: 'Data do Fim',
    },
  ];

  const columnsRegistroAmbiente: ColumnsProps[] = [
    {
      dataField: 'numeroIdentificacaoTrabalhador',
      text: 'Identificação do Trabalhador',
    },
    {
      dataField: 'conselhoClasse',
      text: 'Conselho de Classe',
    },
    {
      dataField: 'profissionalLegalmenteHabilitado',
      text: 'Profissional Legalmente Habilitado',
    },
    {
      dataField: 'dataInicio',
      text: 'Data de Início',
    },
    {
      dataField: 'dataFim',
      text: 'Data do Fim',
    },
  ];

  const columnsExameClinico: ColumnsProps[] = [
    enumColumn(
      {
        dataField: 'tipoExame',
        text: 'Tipo de Exame',
      },
      TIPO_EXAME_MEDICO_ENUM_VALUES
    ),
    {
      dataField: 'natureza',
      text: 'Natureza',
    },
    enumColumn(
      {
        dataField: 'exameReferencialSequencial',
        text: 'Exame (R/S)',
      },
      EXAME_REFERENCIAL_SEQUENCIAL_ENUM_VALUES
    ),
    enumColumn(
      {
        dataField: 'indicacaoResultado',
        text: 'Indicação de Resultado',
      },
      INDICACAO_RESULTADO_ENUM_VALUES
    ),
    {
      dataField: 'dataDoRegistro',
      text: 'Data de inicio',
    },
  ];

  const columnsMonitoracaoBiologica: ColumnsProps[] = [
    {
      dataField: 'numeroIdentificacaoTrabalhador',
      text: 'Identificação do Trabalhador',
    },
    {
      dataField: 'conselhoClasse',
      text: 'Conselho de Classe',
    },
    {
      dataField: 'profissionalLegalmenteHabilitado',
      text: 'Profissional Legalmente Habilitado',
    },
    {
      dataField: 'dataInicio',
      text: 'Data de Início',
    },
    {
      dataField: 'dataFim',
      text: 'Data do Fim',
    },
  ];

  const monitoracaoBiologicaFormProps: DetailManagerFormProps = {
    columns: columnsMonitoracaoBiologica,
    detailView: MonitoracaoBiologicaEditView,
    formProps,
    propName: 'monitoracoesBiologicas',
    title: 'Responsável pela monitoração biólogica',
  };

  const exameClinicoFormProps: DetailManagerFormProps = {
    columns: columnsExameClinico,
    detailView: ExameMedicoClinicoComplementarEditView,
    formProps,
    propName: 'examesMedicoClinicoComplementar',
    title: 'Exames Médicos Clínicos e Complementares',
  };

  const registroAmbienteFormProps: DetailManagerFormProps = {
    columns: columnsRegistroAmbiente,
    detailView: RegistroAmbienteEditView,
    formProps,
    propName: 'registrosAmbientais',
    title: 'Responsável pelos registros ambientais',
  };

  const fatorRiscoFormProps: DetailManagerFormProps = {
    columns: columnsFatorRisco,
    detailView: FatorRiscoEditView,
    formProps,
    propName: 'fatoresRiscos',
    title: 'Fatores de Risco',
  };

  const profissiografiaFormProps: DetailManagerFormProps = {
    columns: columnsProfissiografia,
    detailView: ProfissiografiaEditView,
    formProps,
    propName: 'profissiografias',
    title: 'Profissiografia',
  };

  const lotacaoAtribuicaoFormProps: DetailManagerFormProps = {
    columns: columnsLotacaoAtribuicao,
    detailView: LotacaoAtribuicaoEditView,
    formProps,
    propName: 'lotacoesAtribuicoes',
    title: 'Lotação e atribuições',
  };

  const catRegistradaFormProps: DetailManagerFormProps = {
    columns: columnsCatRegistrada,
    detailView: CatRegistradaEditView,
    formProps,
    propName: 'catsRegistradas',
    title: 'CAT Registrada',
  };

  return (
    <WindowComponent title="Manutenção de Perfil Profissiográfico Previdenciário">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <InputGroupInline formProps={formProps} id="empresaMFP.id" hidden={true} />
        <Row>
          <Col>
            <VinculoIS
              formProps={{ ...formProps, validation: { required: true } }}
              id="vinculo"
              inputSearchProps={{ selectProps: { isMulti: false } }}
              label="Vínculo"
              labelWidth={defaultWidth}
              placeholder="Selecione o Vínculo"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <Nav tabs={true}>
              <NavItem>
                <NavLink className={cx({ active: activeTab === 1 })} tabIndex={1} onClick={toggleTab}>
                  I - Seção de Dados Administrativos
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink className={cx({ active: activeTab === 2 })} tabIndex={2} onClick={toggleTab}>
                  II - Seção de Registros Ambientais
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink className={cx({ active: activeTab === 3 })} tabIndex={3} onClick={toggleTab}>
                  III - Seção do Resultados de Monitoração Biológica
                </NavLink>
              </NavItem>
            </Nav>
            <TabContent className="mb-1" activeTab={activeTab}>
              <TabPane tabId={1} className="mb-0 p-3">
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { maxLength: 30 } }}
                      id="brPdh"
                      isPrependLabel={true}
                      label="Beneficiário Reabilitado/Portador de Deficiência Habilitado"
                      placeholder="Informe o Beneficiário Reabilitado/Portador de Deficiência Habilitado"
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { maxLength: 15 } }}
                      id="regimeRevezamento"
                      isPrependLabel={true}
                      label="Regime de Revezamento"
                      placeholder="Informe o Regime de Revezamento"
                    />
                  </Col>
                </Row>
                <Accordion>
                  <AccordionItem title="CAT Registrada">
                    <DetailManagerForm {...props} defaultSortDirection={OrderType.DESC} {...catRegistradaFormProps} keyField="rowIndex" />
                  </AccordionItem>
                  <AccordionItem title="Lotação e atribuição">
                    <DetailManagerForm {...props} defaultSortDirection={OrderType.DESC} {...lotacaoAtribuicaoFormProps} keyField="rowIndex" />
                  </AccordionItem>
                  <AccordionItem title="Profissiografia">
                    <DetailManagerForm {...props} defaultSortDirection={OrderType.DESC} {...profissiografiaFormProps} keyField="rowIndex" />
                  </AccordionItem>
                </Accordion>
              </TabPane>
              <TabPane tabId={2} className="mb-0 p-3">
                <Accordion>
                  <AccordionItem title="CAT Registrada">
                    <Row>
                      <Col>
                        <CustomInput
                          type="switch"
                          id="medidaProtecao"
                          name="medidaProtecao"
                          label="Foi tentada a implementação de medidas de proteção coletiva"
                          innerRef={formProps.register}
                        />
                      </Col>
                    </Row>
                    <Row>
                      <Col>
                        <CustomInput
                          type="switch"
                          id="condicoesFuncionamento"
                          name="condicoesFuncionamento"
                          label="Foram observadas as condições de funcionamento e do uso ininterrupto do EPI ao longo do tempo"
                          innerRef={formProps.register}
                        />
                      </Col>
                    </Row>
                    <Row>
                      <Col>
                        <CustomInput
                          type="switch"
                          id="prazoValidade"
                          name="prazoValidade"
                          label="Foi observado o prazo de validade, conforme Certificado de Aprovação-CA do MTE"
                          innerRef={formProps.register}
                        />
                      </Col>
                    </Row>
                    <Row>
                      <Col>
                        <CustomInput
                          type="switch"
                          id="periodicidadeTroca"
                          name="periodicidadeTroca"
                          label="Foi observada a periodicidade de troca definida pelos programas ambientais"
                          innerRef={formProps.register}
                        />
                      </Col>
                    </Row>
                    <Row className="mb-3">
                      <Col>
                        <CustomInput
                          type="switch"
                          id="higienizacao"
                          name="higienizacao"
                          label="Foi observada a higienização"
                          innerRef={formProps.register}
                        />
                      </Col>
                    </Row>
                    <DetailManagerForm {...props} defaultSortDirection={OrderType.DESC} {...fatorRiscoFormProps} keyField="rowIndex" />
                  </AccordionItem>
                  <AccordionItem title="Responsável pelos registros ambientais">
                    <DetailManagerForm {...props} defaultSortDirection={OrderType.DESC} {...registroAmbienteFormProps} keyField="rowIndex" />
                  </AccordionItem>
                </Accordion>
              </TabPane>
              <TabPane tabId={3} className="mb-0 p-3">
                <Accordion>
                  <AccordionItem title="Exames Médicos Clínicos e Complementares">
                    <DetailManagerForm {...props} defaultSortDirection={OrderType.DESC} {...exameClinicoFormProps} keyField="rowIndex" />
                  </AccordionItem>
                  <AccordionItem title="Responsável pela monitoração biólogica">
                    <DetailManagerForm {...props} defaultSortDirection={OrderType.DESC} {...monitoracaoBiologicaFormProps} keyField="rowIndex" />
                  </AccordionItem>
                </Accordion>
              </TabPane>
            </TabContent>
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default PppEditView;
