import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType, onlyNumber } from 'summer';
import { LotacaoAtribuicao } from '../../../../models/dtos/mfp/lotacaoAtribuicao';
import CargoIS from '../../../components/mfp/cargo/CargoIS';
import CboIS from '../../../components/mfp/cboIS/CboIS';

const maxWidthLabel = 150;

const LotacaoAtribuicaoEditView: FC<DetailEditViewFormProps<LotacaoAtribuicao>> = props => {
  const formProps = useForm<LotacaoAtribuicao>({
    defaultValues: { ...props.selectedItem },
    mode: 'onBlur',
  });

  const validateExtras = () => {
    const erros = [];

    if (formProps.getValues().dataInicio > formProps.getValues().dataFim) {
      erros.push('A data de inicio não pode ser maior que a data do fim');
    }

    return erros;
  };

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps} validateExtras={validateExtras}>
      <Row>
        <Col>
          <CboIS
            id="cbo"
            isPrependLabel={true}
            labelWidth={maxWidthLabel}
            label="CBO"
            placeholder="Informe o CBO."
            formProps={{ ...formProps, validation: { required: true } }}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <CargoIS
            id="cargo"
            isPrependLabel={true}
            labelWidth={maxWidthLabel}
            label="Cargo"
            placeholder="Informe o Cargo."
            formProps={{ ...formProps, validation: { required: true } }}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true } }}
            id="dataInicio"
            label="Data de Início"
            placeholder="Informe a Data de Início"
            isPrependLabel={true}
            labelWidth={maxWidthLabel}
            type={InputType.DATE}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true } }}
            id="dataFim"
            label="Data do Fim"
            placeholder="Informe a Data do Fim"
            labelWidth={maxWidthLabel}
            isPrependLabel={true}
            type={InputType.DATE}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 14 } }}
            id="cnpjCei"
            label="CNPJ/CEI"
            isPrependLabel={true}
            labelWidth={maxWidthLabel}
            placeholder="informe o CNPJ/CEI"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 15 } }}
            id="setor"
            isPrependLabel={true}
            labelWidth={maxWidthLabel}
            label="Setor"
            placeholder="Informe o Setor"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 30 } }}
            id="funcao"
            isPrependLabel={true}
            labelWidth={maxWidthLabel}
            label="Função"
            placeholder="Informe a Funçào"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { ...onlyNumber(0, 2) } }}
            id="codigoGfip"
            isPrependLabel={true}
            labelWidth={maxWidthLabel}
            label="Código GFIP"
            placeholder="Informe o Código GFIP"
          />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default LotacaoAtribuicaoEditView;
