import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { Col, CustomInput, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType, onlyNumber } from 'summer';
import { FatorRisco } from '../../../../models/dtos/mfp/fatorRisco';
import { TIPO_FATOR_RISCO_ENUM_VALUES } from '../../../../models/enumerated/ger/tipoFatorRiscoEnum';

const FatorRiscoEditView: FC<DetailEditViewFormProps<FatorRisco>> = props => {
  const formProps = useForm<FatorRisco>({
    defaultValues: { ...props.selectedItem },
    mode: 'onBlur',
  });

  const validateExtras = () => {
    const erros = [];

    if (formProps.getValues().dataInicio > formProps.getValues().dataFim) {
      erros.push('A data de inicio não pode ser maior que a data do fim');
    }

    return erros;
  };

  const maxWidthLabel = 230;

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps} validateExtras={validateExtras}>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true } }}
            id="dataInicio"
            label="Data de Início"
            labelWidth={maxWidthLabel}
            placeholder="Informe a Data de Início"
            isPrependLabel={true}
            type={InputType.DATE}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true } }}
            id="dataFim"
            label="Data do Fim"
            labelWidth={maxWidthLabel}
            placeholder="Informe a Data do Fim"
            isPrependLabel={true}
            type={InputType.DATE}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="tipoFatorRisco"
            label="Fator de Risco"
            isPrependLabel={true}
            labelWidth={maxWidthLabel}
            placeholder="informe o Fator de Risco"
            formProps={formProps}
            inputDropDownProps={{
              optionsProvider: TIPO_FATOR_RISCO_ENUM_VALUES,
              selectProps: { isClearable: false },
            }}
            type={InputType.DROP_DOWN}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 40 } }}
            id="fatorRisco"
            isPrependLabel={true}
            label="Descrição Fator de Risco"
            labelWidth={maxWidthLabel}
            placeholder="Informe a descrição do fator de risco"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 15 } }}
            id="tecnicaUtilizada"
            isPrependLabel={true}
            labelWidth={maxWidthLabel}
            label="Técnica Utilizada"
            placeholder="Informe a técnica utilizada"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 15 } }}
            id="intensidadeConcentracao"
            isPrependLabel={true}
            labelWidth={maxWidthLabel}
            label="Intensidade/Concentração"
            placeholder="Informe a técnica utilizada"
          />
        </Col>
      </Row>
      <Row className="mb-3">
        <Col>
          <CustomInput type="switch" id="epcEficaz" name="epcEficaz" label="EPC Eficaz" innerRef={formProps.register} />
        </Col>
        <Col>
          <CustomInput type="switch" id="epiEficaz" name="epiEficaz" label="EPI Eficaz" innerRef={formProps.register} />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: onlyNumber(0, 2) }}
            id="certificadoAprovacaoEpi"
            isPrependLabel={true}
            labelWidth={maxWidthLabel}
            label="Certificado de Aprovação EPI"
            placeholder="Informe o Certificado de aprovação EPI"
          />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default FatorRiscoEditView;
