import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType } from 'summer';
import { RegistroAmbiente } from '../../../../models/dtos/mfp/registroAmbiente';

const RegistroAmbienteEditView: FC<DetailEditViewFormProps<RegistroAmbiente>> = props => {
  const formProps = useForm<RegistroAmbiente>({
    defaultValues: { ...props.selectedItem },
    mode: 'onBlur',
  });

  const validateExtras = () => {
    const erros = [];

    if (formProps.getValues().dataInicio > formProps.getValues().dataFim) {
      erros.push('A data de inicio não pode ser maior que a data do fim');
    }

    return erros;
  };

  const maxWidthLabel = 270;

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps} validateExtras={validateExtras}>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true } }}
            id="dataInicio"
            label="Data de Início"
            labelWidth={maxWidthLabel}
            placeholder="Informe a Data de Início"
            isPrependLabel={true}
            type={InputType.DATE}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true } }}
            id="dataFim"
            label="Data do Fim"
            labelWidth={maxWidthLabel}
            placeholder="Informe a Data do Fim"
            isPrependLabel={true}
            type={InputType.DATE}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 11 } }}
            id="numeroIdentificacaoTrabalhador"
            labelWidth={maxWidthLabel}
            isPrependLabel={true}
            label="Número Identificação Trabalhador"
            placeholder="Informe o número de identificação do trabalhador"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 30 } }}
            id="profissionalLegalmenteHabilitado"
            labelWidth={maxWidthLabel}
            isPrependLabel={true}
            label="Profissional Legalmente Habilitado"
            placeholder="Informe o profissional legalmente habilitado"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 9 } }}
            id="conselhoClasse"
            labelWidth={maxWidthLabel}
            isPrependLabel={true}
            label="Número Conselho de Classe"
            placeholder="Informe o número do conselho de classe"
          />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default RegistroAmbienteEditView;
